* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  position: relative;
  width: 100%;
  max-height: 100%;
  padding: 0 20px;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: 1fr;
  box-shadow: 0px 6px 10px #cccccc;
  background-color: #ffffff;
}
@media (max-width: 800px) {
  .header {
    grid-template-columns: 1fr;
    position: fixed;
    z-index: 2;
    overflow-X: auto;
  }
}
.page-title {
  margin: 15px 0;
  font-size: 28px;
  font-family: 'Akshar', sans-serif;
  font-weight: bold;
  color: #000000;
  white-space: nowrap;
}
@media (max-width: 800px) {
  .page-title {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 1;
    margin: 0;
    font-size: 32px;
  }
}
.nav {
  display: flex;
  align-items: center;
  justify-content: end;
}
@media (max-width: 800px) {
  .nav {
    flex-direction: column;
    align-items: end;
    justify-content: start;
    gap: 20px;
  }
}
.nav__button {
  display: none;
  border: none;
  background-color: transparent;
}
@media (max-width: 800px) {
  .nav__button {
    display: block;
    width: 80px;
    height: 80px;
  }
}
.nav__list {
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 20px;
}
@media (max-width: 800px) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  display: flex;
  flex-direction: column;
  align-items: end;
  height: min-content;
  padding-bottom: 20px;
}
.nav__item {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__link {
  height: 100%;
  font-size: 19px;
  white-space: nowrap;
  position: relative;
  padding: 15px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000000;
  text-decoration: none;
  opacity: 0.8;
}
@media (max-width: 800px) {
  .nav__link {
    font-size: 22px;
  }
}
.nav__link--active {
  opacity: 1;
  pointer-events: none;
}
.nav__link--active::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #cccccc;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 1;
}
.nav__link:hover::before,
.nav__link:focus::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #cccccc;
  animation-duration: 1s;
  animation-name: linkanimate;
}
@keyframes linkanimate {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
.nav__shopping-cart {
  width: 35px;
  height: 35px;
}
.btn-icon {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.btn-icon--active {
  transform: rotate(45deg);
}
.btn-icon__line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.btn-icon__line--top {
  stroke-dasharray: 40 139;
}
.btn-icon__line--bottom {
  stroke-dasharray: 40 180;
}
.btn-icon--active .btn-icon__line--top {
  stroke-dashoffset: -98px;
}
.btn-icon--active .btn-icon__line--bottom {
  stroke-dashoffset: -138px;
}
.main {
  flex-grow: 1;
  padding: 100px 20px;
}
.footer {
  padding: 20px;
  box-shadow: 0px -6px 10px #cccccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 600px) {
  .footer {
    flex-direction: column;
    gap: 30px;
  }
}
.social-list {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 20px;
  list-style: none;
}
@media (max-width: 600px) {
  .social-list {
    flex-direction: column;
  }
}
.social-list__link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 0;
  text-decoration: none;
  color: #000000;
  opacity: 0.8;
  font-size: 17px;
}
.social-list__link:hover,
.social-list__link:focus {
  opacity: 1;
}
.social-list__icon {
  opacity: 0.5;
}
.social-list__icon:hover,
.social-list__icon:focus {
  opacity: 1;
}
.rights {
  font-size: 15px;
  opacity: 0.7;
}
.banner {
  width: 100%;
  height: 300px;
  position: relative;
}
.banner__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.banner__info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.6);
}
.banner__title {
  font-size: 21px;
  font-weight: bold;
}
.banner__text {
  font-size: 18px;
}
